"use client"

import { useState } from "react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Search, MoreHorizontal } from "lucide-react"
import { Search, Plus, MoreVertical } from "lucide-react"

const mockCustomers = [
  { id: 1, name: "Amit Kumar", upiId: "amit@upi", stamps: 12, visits: 15, lastVisit: "2 days ago" },
  { id: 2, name: "Priya Singh", upiId: "priya@upi", stamps: 8, visits: 10, lastVisit: "1 day ago" },
  { id: 3, name: "Vikram Patel", upiId: "vikram@upi", stamps: 5, visits: 6, lastVisit: "3 days ago" },
  { id: 4, name: "Neha Sharma", upiId: "neha@upi", stamps: 20, visits: 25, lastVisit: "Today" },
]

export default function CustomersList() {
  const [search, setSearch] = useState("")

  const filtered = mockCustomers.filter(
    (c) =>
      c.name.toLowerCase().includes(search.toLowerCase()) ||
      c.upiId.toLowerCase().includes(search.toLowerCase())
  )

  return (
    <div className="space-y-2">
      <div>
        {/* Search */}
        <div className="relative max-w-xs" style={{ background: "linear-gradient(to bottom right, #faf5ff, #ffffff)" }}>
          <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search customers"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="h-9 pl-8 text-sm"
          />
        </div>

        {/* add button  */}
        <Button className="gap-2">
          <Plus size={20} />
          Add Customer
        </Button>

      </div>

      {/* Table */}
      <Card className="overflow-hidden card">
        <Table>
          <TableHeader className="bg-muted/40 sticky top-0 z-10">
            <TableRow>
              <TableHead>Customer</TableHead>
              <TableHead>UPI</TableHead>
              <TableHead className="text-center">Stamps</TableHead>
              <TableHead className="text-center">Visits</TableHead>
              <TableHead>Last Visit</TableHead>
              <TableHead className="w-10" />
            </TableRow>
          </TableHeader>

          <TableBody>
            {filtered.map((c) => (
              <TableRow
                key={c.id}
                className="hover:bg-muted/30 transition-colors"
              >
                {/* Customer */}
                <TableCell>
                  <div className="flex items-center gap-3">
                    <div className="h-8 w-8 rounded-full bg-primary/10 text-primary flex items-center justify-center text-xs font-semibold">
                      {c.name[0]}
                    </div>
                    <span className="font-medium">{c.name}</span>
                  </div>
                </TableCell>

                {/* UPI */}
                <TableCell className="text-muted-foreground">
                  {c.upiId}
                </TableCell>

                {/* Stamps */}
                <TableCell className="text-center">
                  <Badge variant="default" className="rounded-full px-2 py-0.5">
                    {c.stamps}
                  </Badge>
                </TableCell>

                {/* Visits */}
                <TableCell className="text-center">
                  {c.visits}
                </TableCell>

                {/* Last Visit */}
                <TableCell className="text-muted-foreground">
                  {c.lastVisit}
                </TableCell>

                {/* Actions */}
                <TableCell className="text-right">
                  <Button
                    variant="ghost"
                    size="icon"
                    className="h-8 w-8 text-muted-foreground hover:text-foreground"
                  >
                    <MoreHorizontal size={16} />
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        {filtered.length === 0 && (
          <div className="py-6 text-center text-sm text-muted-foreground">
            No customers found
          </div>
        )}
      </Card>
    </div>
  )
}
